/* Sidebar styles */
.sidebar {
    width: 250px;
    background-color: #333;
    color: white;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -250px;  /* Initially hidden */
    transition: left 0.3s ease; /* Smooth transition for the left property */
  }

  /* Close button styles */
  .sidebar-close-btn {
    position: absolute;
    top: 20px;
    right: 20px;
    background: none;
    color: white;
    border: none;
    font-size: 2rem;
    cursor: pointer;
    transition: color 0.3s ease;
  }
  
  .sidebar-close-btn:hover {
    color: #ccc;
  }
  
  /* Button to toggle the sidebar */
  .sidebar-toggle-btn {
    width: 50px;
    height: 50px;
    position: fixed;
    top: 5px;
    left: 5px;
    padding: 10px;
    background-color: #007BFF;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .sidebar-toggle-btn:hover {
    background-color: #0056b3;
  }
  