.project-list ul {
  list-style-type: none;
  padding: 20px;
}

.project-list ul li {
  margin: 10px 0;
}

.project-list ul li a {
  text-decoration: none;
  color: #ccc;
  font-size: 1.2rem;
  display: block;
  padding: 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.project-list ul li a.active,
.project-list ul li a:hover {
  background-color: #00ff6a;
  color: white;
}